import React, { useState, useCallback, useMemo } from 'react';
import styled from 'styled-components';
import { _ } from '@splunk/ui-utils/i18n';
import TransitionOpen from '@splunk/react-ui/TransitionOpen';
import ChevronRight from '@splunk/react-icons/ChevronRight';
import ChevronDown from '@splunk/react-icons/ChevronDown';

const Group = styled.div``;
const Header = styled.div`
    padding: 10px 15px 0px 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
`;
const Label = styled.span`
    font-weight: 500;
    font-size: 13px;
`;

const IconContainer = styled.span`
    width: 20px;
    height: 20px;
    text-align: center;
    &:hover {
        cursor: pointer;
    }
`;

interface CollapsibleGroupProps {
    label?: string;
    open?: boolean;
    'data-test'?: string;
    children?: React.ReactNode;
}

const CollapsibleGroup = ({
    label,
    open = false,
    'data-test': dataTest = 'collapsible-group',
    children,
}: CollapsibleGroupProps): JSX.Element => {
    const [isOpen, setIsOpen] = useState(open);

    const handleClick = useCallback(() => {
        setIsOpen(!isOpen);
    }, [isOpen]);

    const icon = useMemo(
        () =>
            isOpen ? (
                <ChevronDown screenReaderText={_('Collapse')} />
            ) : (
                <ChevronRight screenReaderText={_('Expand')} />
            ),
        [isOpen]
    );

    return (
        <Group data-test={dataTest} data-open={isOpen}>
            <Header data-test="collapsible-group-header" onClick={handleClick}>
                <Label data-test="collapsible-group-label">{label}</Label>
                <IconContainer>{icon}</IconContainer>
            </Header>
            <TransitionOpen animation="slideFromTop" open={isOpen}>
                {children}
            </TransitionOpen>
        </Group>
    );
};

export default CollapsibleGroup;
